
.present {
  width: 100%;
  display: flex;
  animation-duration: 0.5s;
  .contentBox {
    width: 100%;
    display: flex;
    justify-content: center;
    .content {
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;

      background: #f6f6f6;

      .content-top {
        width: 100%;
        display: flex;
        justify-content: space-between;
        background: #fff;
        .title {
          font-size: 12px;
          font-family: PingFang SC-中等, PingFang SC;
          font-weight: normal;
          color: #ffb300;
          line-height: 21px;
          margin: 13px 0 13px 10px;
        }
        .close {
          font-size: 14px;
          font-family: PingFang SC-中等, PingFang SC;
          font-weight: normal;
          color: #ff2a00;
          line-height: 21px;
          margin: 13px 19px 13px 0;
        }
      }
      .content-center {
        width: 100%;
        overflow-y: scroll;
        -webkit-overflow-scrolling: touch;
        .information {
          display: flex;
          flex-wrap: wrap;
          margin: 5px 0 0;
          .container {
            display: flex;
            margin: 0 0 10px 11px;
            .gift {
              width: 0.8rem;
              height: 1.13rem;
              display: flex;
              flex-direction: column;
              align-items: center;
              .gift-top {
                width: 100%;
                display: flex;
                flex-direction: column;
                align-items: center;
                border-bottom: none;
                border-radius: 8px 8px 0 0;
                img {
                  width: 68px;
                  height: 68px;
                  margin: 6px 0 0;
                }
                .title {
                  font-size: 13px;
                  font-family: PingFang SC-粗体, PingFang SC;
                  font-weight: normal;
                  color: #ff2a00;
                  line-height: 10px;
                  margin: 6px 0;
                }
                .price {
                  font-size: 10px;
                  zoom: 0.8;
                  font-family: PingFang SC-中等, PingFang SC;
                  font-weight: normal;
                  color: #ffb300;
                  line-height: 13px;
                  margin: 1px 0;
                }
                &.active {
                  border: 1px solid #ff2a00;
                  background: #fff;
                }
              }
              .gift-bottom {
                display: flex;
                .give {
                  width: 0.8rem;
                  height: 0.24rem;
                  background: #ff2a00;
                  border-radius: 0 0 8px 8px;
                  font-size: 13px;
                  font-family: PingFang SC-粗体, PingFang SC;
                  font-weight: normal;
                  color: #ffffff;
                  line-height: 13px;
                  border: 1px solid #ff2a00;
                }
              }
            }
          }
        }
      }
      .content-center::-webkit-scrollbar {
        display: none;
      }
      .content-bottom {
        width: 100%;
        display: flex;
        position: fixed;
        left: 0;
        bottom: 0;
        .info {
          width: 100%;
          display: flex;
          padding: 15px 0;
          background: #fff;
          .title {
            font-size: 15px;
            font-family: PingFang SC-粗体, PingFang SC;
            font-weight: normal;
            color: #323332;
            line-height: 21px;
            margin: 0 0 0 10px;
          }
          .number {
            font-size: 15px;
            font-family: PingFang SC-粗体, PingFang SC;
            font-weight: normal;
            color: #ffb300;
            line-height: 21px;
          }
        }
      }
    }
  }
}
