
.home {
  min-height: 100vh;
  background: #eee;
  .headerBox {
    display: flex;
    justify-content: center;
    .header {
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      margin: 7px 0 0;
      position: absolute;
      top: 72px;
      z-index: 10;
      .header-top {
        width: 100%;
        display: flex;
        justify-content: flex-end;
        .rank {
          width: 80px;
          height: 30px;
          display: flex;
          background: #e4e4e4;
          // background: rgba(0, 0, 0, 0.26);
          border-radius: 41px;
          align-items: center;
          justify-content: center;
          margin-right: 10px;
          img {
            width: 13px;
            height: 13px;
          }
          .message {
            font-size: 11px;
            zoom: 1;
            font-family: PingFang SC-中等, PingFang SC;
            font-weight: normal;
            color: #ff2a00;
          }
        }
      }
      .header-bottom {
        width: 100%;
        display: flex;
        justify-content: flex-end;
        .photo {
          display: flex;
          align-items: center;
          width: 80px;
          height: 30px;
          // background: rgba(0, 0, 0, 0.26);
          border-radius: 41px;
          justify-content: center;
          margin-right: 10px;
          margin-top: 10px;
          background: #e4e4e4;
          .text {
            width: 12px;
            height: 12px;
            margin: 0 0 0 9px;
          }
          .message {
            font-size: 11px;
            font-family: PingFang SC-中等, PingFang SC;
            font-weight: normal;
            color: #ff2a00;
            // zoom: 0.9;
            // margin: 0 5px 0 1px;
          }
          img {
            width: 13px;
            height: 13px;
          }
        }
      }
    }
  }
  .contentBox {
    display: flex;
    justify-content: center;
    .content {
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      .content-top {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        .movie {
          width: 100%;
          // height: 2.23rem;
          height: 0px;
          // background: url("@/assets/bg.png") no-repeat;
          // background-size: 100%;
        }
        .information {
          width: 95%;
          display: flex;
          flex-direction: column;
          position: absolute;
          .container {
            width: 1.95rem;
            display: flex;
            height: 44px;
            background: rgba(255, 42, 0, 0.34);
            border-radius: 230px;
            .profile {
              width: 36px;
              height: 36px;
              margin: 4px 0 4px 5px;
            }
            .info {
              display: flex;
              flex-direction: column;
              .name {
                width: 0.9rem;
                font-size: 15px;
                font-family: PingFang SC-粗体, PingFang SC;
                font-weight: normal;
                color: #ffffff;
                line-height: 13px;
                margin: 8px 0 4px 4px;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
              }
              .message {
                font-size: 12px;
                font-family: PingFang SC-常规, PingFang SC;
                font-weight: normal;
                color: #ffffff;
                line-height: 13px;
                margin: 0 0 0 5px;
              }
            }
            .gift {
              display: flex;
              align-items: flex-end;
              img {
                width: 68px;
                height: 68px;
              }
            }
          }
        }
      }
      .content-center {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        background: #fff;
        .introduce {
          width: 92%;
          display: flex;
          flex-direction: column;
          align-items: center;
          margin: 5px 0;
          .introduce-top {
            width: 100%;
            display: flex;
            .stream {
              display: flex;
              .stream-status {
                display: flex;
                width: 0.58rem;
                height: 0.26rem;
                display: flex;
                align-items: center;
                border-radius: 41px;
                background: #f4f4f4;
                img {
                  width: 10px;
                  height: 10px;
                  margin: 0 3px 0 6px;
                }
                .message {
                  font-size: 0.786em;
                  font-family: PingFang SC-中等, PingFang SC;
                  font-weight: normal;
                  color: #ff2a00;
                  zoom: 0.9;
                }
              }
              .stream-theme {
                width: 2.9rem;
                display: flex;
                align-items: center;
                margin: 0 0 0 5px;
                .title {
                  font-size: 16px;
                  font-family: PingFang SC-中等, PingFang SC;
                  font-weight: normal;
                  color: #323332;
                  overflow: hidden;
                  text-overflow: ellipsis;
                  white-space: nowrap;
                  line-height: 16px;
                }
              }
            }
          }
          .introduce-bottom {
            width: 100%;
            display: flex;
            justify-content: flex-end;
            align-items: center;
            margin: 6px 0 0;
            .introduce-bottom-left {
              display: flex;
              .brief {
                display: flex;
                border-bottom: 2px solid #999999;
                padding: 0 0 5px 0;
                .message {
                  font-size: 13px;
                  font-family: PingFang SC-中等, PingFang SC;
                  font-weight: normal;
                  color: #999999;
                  line-height: 13px;
                }
              }
              img {
                width: 6px;
                height: 11px;
                margin: 2px 0 0 7px;
              }
            }
            .introduce-bottom-right {
              display: flex;
              .browse {
                display: flex;
                img {
                  width: 12px;
                  height: 12px;
                }
                .number {
                  font-size: 13px;
                  font-family: PingFang SC-中等, PingFang SC;
                  font-weight: normal;
                  color: #999999;
                  line-height: 13px;
                  margin: 0 0 0 4px;
                }
              }
              .like {
                display: flex;
                margin: 0 0 0 9px;
                img {
                  width: 12px;
                  height: 12px;
                }
                .number {
                  font-size: 13px;
                  font-family: PingFang SC-中等, PingFang SC;
                  font-weight: normal;
                  color: #999999;
                  line-height: 13px;
                  margin: 0 0 0 3px;
                }
              }
            }
          }
        }
        .review {
          width: 100%;
          display: flex;
        }
      }
      .content-bottom {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        position: fixed;
        bottom: 0;
        .send {
          width: 95%;
          min-height: 0.5rem;
          display: flex;
          justify-content: space-between;
          align-items: center;
          .send-left {
            width: 2.59rem;
            height: 0.4rem;
            display: flex;
            justify-content: space-between;
            align-items: center;
            background: #fff;
            border-radius: 170px;
            .text {
              display: inline-block;
              width: 80%;
              height: 0.3rem;
              margin: 0 0 0 14px;
              border: none;
              font-size: 14px;
              font-family: PingFang SC-中等, PingFang SC;
              font-weight: normal;
              line-height: 14px;
            }
            .text::-webkit-input-placeholder {
              color: #999999;
            }
            img {
              width: 18px;
              height: 18px;
              margin: 0 14px 0 0;
            }
          }
          .send-center {
            display: flex;
            align-items: center;
            .add {
              width: 0.8rem;
              height: 0.36rem;
              background: linear-gradient(270deg, #ff5736 0%, #ff2a00 100%);
              border-radius: 34px;
              font-size: 14px;
              font-family: PingFang SC-中等, PingFang SC;
              font-weight: normal;
              color: #ffffff;
              line-height: 14px;
            }
          }
          .send-right {
            display: flex;
            align-items: center;
            .gift {
              display: flex;
              margin: 0 23px 0 0;
              img {
                width: 26px;
                height: 26px;
              }
            }
            .more {
              display: flex;
              margin: 0 10px 0 0;
              img {
                width: 26px;
                height: 26px;
              }
            }
          }
        }
        .expression {
          overflow-y: scroll;
          -webkit-overflow-scrolling: touch;
          ::v-deep .emoji-mart {
            width: 100% !important;
            height: 160px;
            border: none;
            background: #eee;
            .emoji-mart-category-label {
              display: none;
            }
          }
        }
      }
      @supports (bottom: constant(safe-area-inset-bottom)) or
        (bottom: env(safe-area-inset-bottom)) {
        .content-bottom {
          padding-bottom: constant(safe-area-inset-bottom);
          padding-bottom: env(safe-area-inset-bottom);
        }
      }
      .restBox {
        display: flex;
        justify-content: center;
        .rest {
          width: 100%;
          display: flex;
          flex-direction: column;
          .rest-top {
            display: flex;
            justify-content: space-between;
            background: #fff;
            .account {
              display: flex;
              margin: 13px 0 13px 20px;
              .title {
                font-size: 14px;
                font-family: PingFang SC-中等, PingFang SC;
                font-weight: normal;
                color: #333333;
                line-height: 21px;
              }
              .number {
                font-size: 14px;
                font-family: PingFang SC-中等, PingFang SC;
                font-weight: normal;
                color: #ffb300;
                line-height: 21px;
              }
            }
            .close {
              width: 28px;
              height: 21px;
              font-size: 14px;
              font-family: PingFang SC-中等, PingFang SC;
              font-weight: normal;
              color: #ff2a00;
              line-height: 21px;
              margin: 13px 20px 13px 0;
            }
          }
          .rest-center {
            display: flex;
            flex-direction: column;
            background: #f6f6f6;
            padding: 0 0 28px;
            .chat {
              display: flex;
              flex-direction: column;
              margin: 23px 0 0 20px;
              .chat-top {
                display: flex;
                .title {
                  font-size: 14px;
                  font-family: PingFang SC-粗体, PingFang SC;
                  font-weight: normal;
                  color: #ff2a00;
                  line-height: 10px;
                }
              }
              .chat-bottom {
                display: flex;
                flex-direction: column;
                img {
                  width: 50px;
                  height: 50px;
                  margin: 18px 0 7px;
                }
                .message {
                  font-size: 12px;
                  font-family: PingFang SC-中等, PingFang SC;
                  font-weight: normal;
                  color: #333333;
                  line-height: 12px;
                  margin: 0 0 0 6px;
                }
              }
            }
            .streaming {
              display: flex;
              flex-direction: column;
              margin: 22px 0 0 20px;
              .streaming-top {
                display: flex;
                .title {
                  font-size: 14px;
                  font-family: PingFang SC-粗体, PingFang SC;
                  font-weight: normal;
                  color: #ff2a00;
                  line-height: 10px;
                }
              }
              .streaming-bottom {
                display: flex;
                .gold_convert,
                .winning_record,
                .give_record {
                  display: flex;
                  flex-direction: column;
                  margin: 0 22px 0 0;
                  img {
                    width: 50px;
                    height: 50px;
                    margin: 20px 0 5px;
                  }
                  .message {
                    font-size: 12px;
                    font-family: PingFang SC-中等, PingFang SC;
                    font-weight: normal;
                    color: #333333;
                    line-height: 12px;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
  ::v-deep .van-popup .van-icon {
    color: black;
    font-weight: bold;
  }
}
