
.barrage {
  width: 100%;
  display: flex;
  justify-content: center;
  .comment {
    width: 100%;
    display: flex;
    justify-content: center;
    background: #f6f6f6;
    .comment-wrap {
      width: 100%;
      overflow-y: scroll;
      -webkit-overflow-scrolling: touch;
      .content {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 5px 0 0;
        .container {
          width: 95%;
          display: flex;
          flex-direction: column;
          align-items: center;
          margin: 0 0 5px 0;
          .container-top {
            width: 100%;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            .timeBox {
              margin-top: 16px;
              width: 118px;
              height: 18px;
              font-size: 12px;
              font-family: PingFang SC-中等, PingFang SC;
              font-weight: normal;
              color: #999999;
              line-height: 18px;
            }
            .information {
              background: rgba(0, 0, 0, 0.3);
              border-radius: 6px;
              padding: 10px;
              max-width: calc(100% - 100px);
              .profile {
                width: 26px;
                height: 26px;
                border-radius: 50%;
                vertical-align: middle;
              }
              .adminLabel {
                width: 35px;
                height: 14px;
                background: #ffc700;
                border-radius: 2px 2px 2px 2px;
                opacity: 1;
                font-size: 11px;
                font-family: PingFang SC-中等, PingFang SC;
                font-weight: normal;
                color: #ffffff;
                line-height: 10px;
                margin-left: 5px;
              }

              .name {
                font-size: 15px;
                font-family: PingFang SC-粗体, PingFang SC;
                font-weight: normal;
                color: #ffffff;
                margin: 0 0 0 5px;
                vertical-align: middle;
              }
              .messageBox {
                margin-left: 30px;
                .message {
                  font-size: 14px;
                  font-family: PingFang SC-中等, PingFang SC;
                  font-weight: normal;
                  color: #ffffff;
                  vertical-align: middle;
                }
                .imgBox {
                  max-width: 160px;
                  max-height: 160px;
                  overflow-y: hidden;
                  border-radius: 6px 6px 6px 6px;
                  opacity: 1;
                  margin-right: 6px;
                  margin-top: 11px;
                  margin-bottom: 9px;
                }
              }
            }
          }
          .container-bottom {
            display: flex;
            flex-direction: column;
            align-items: center;
            margin: 20px 0 10px;
            .gift {
              display: flex;
              background: rgba(255, 42, 0, 0.3);
              border-radius: 12px;
              padding: 5px 20px;
              margin: 0 0 5px 0;
              .name,
              .message {
                font-size: 12px;
                font-family: PingFang SC-中等, PingFang SC;
                font-weight: normal;
                color: #ff2a00;
                line-height: 12px;
              }
              .give {
                font-size: 12px;
                font-family: PingFang SC-中等, PingFang SC;
                font-weight: normal;
                color: #ff2a00;
                line-height: 12px;
                margin: 0 5px;
              }
            }
          }
        }
      }
    }
    .comment-wrap::-webkit-scrollbar {
      display: none;
    }
  }
}
